    <div class="contact-form-container">
      <div class="test_page_cat" v-if="result && image">
        <cat-gif :hideText="true" :gif="image"></cat-gif>
      </div>
      <contact-us-form></contact-us-form>
    </div>
  </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import ContactUsForm from "../contactUsForm";
import CatGif from "../blocks/CatGif";

export default {
  components: {CatGif, ContactUsForm},
  props: {},
  data() {
    return {
      image: 'https://media.giphy.com/media/O4aJ2f5OGZNRK/giphy.gif'
    }
  },
  computed: {
    ...mapState('test', ['result'])
  },
  methods: {
    ...mapActions('modals', ['openBaseModal']),
    signUpForGroup() {
      this.openBaseModal({suggestedGroup: this.result.suggestedGroup})
    },
    signUpForExam() {
      this.openBaseModal({suggestedExam: this.result.suggestedExam})
    },
    makeBotDeepLinkParams() {
      let deepLinkParams = {
        test_result_uuid: this.result?.quizRecordUuid,
        test_result_level_code: this.result?.levelCode,
        utm_source: localStorage.getItem('utm_source'),
        utm_medium: localStorage.getItem('utm_medium'),
        utm_campaign: localStorage.getItem('utm_campaign')
      }
      let paramsString = '';
      for (const param in deepLinkParams) {
        if (deepLinkParams[param] === null || deepLinkParams[param] === 'undefined') {
          //ignore empty utms
          continue;
        }
        paramsString += `|${param}=${deepLinkParams[param]}`
      }
      return paramsString;
    },
    gtmTrackLink(label) {
      this.$gtm.trackEvent({
        event: 'gaEvent',
        eventCategory: 'English Test',
        eventAction: 'Get Result Via Bot Link',
        eventLabel: label
      });
      // eslint-disable-next-line
      fbq('track', 'Lead', {
        content_name: 'Get Result Via Bot Link',
        content_category: 'English Test',
      });
    }
  }
}
</script>
