const CHANGE_LOADING_STATUS = 'CHANGE_LOADING_STATUS';
const state = {
    loading: true
};

const mutations = {
    CHANGE_LOADING_STATUS(state, payload) {
        state.loading = payload.status;
    }
};

const actions = {
    changeLoadingStatus({commit}, payload) {
        if (payload) {
            commit(CHANGE_LOADING_STATUS, payload)
        }
    }
};

const getters = {
    loadingStatus: (state) => {
        return state.loading;
    }
};

export default {
    namespaced: false,
    state,
    mutations,
    actions,
    getters
}
