                <li>
                  <a target="_blank" href="#">Про нас</a>
                </li>
                <li>
                  <a target="_blank" href="#">Для дітей</a>
                </li>
                <li><a target="_blank" href="#">Для дорослих</a></li>
              </ul>
            </nav>
          </div>
          <div class="header-contact" v-if="!showMobileMenu">
            <span class="header-contact-phone">
              <a :href="$getContactData('telephoneUrl')"><span class="header-contact-phone_code">+38 </span>{{$getContactData('telephone') }}</a>
              <span class="header-contact-phone_text">Зателефонуйте нам</span>
