              <rect x="6.21167" width="1" height="24" transform="rotate(15 6.21167 0)" fill="#DBDDE3"/>
            </svg>
            <div class="header-content-links">
              <a class="header-content-link" :href="viberBotUrl" target="_blank"><img src="../../images/icons.svg#icon-viber" alt="icon-viber"></a>
              <a class="header-content-link" :href="telegram" target="_blank"><img src="../../images/icons.svg#icon-telegram" alt="icon-telegram"></a>
            </div>
          </div>
          <div class="hamburger">
            <a href="#" @click.prevent="toggleMobileMenu()">
              <img v-if="!showMobileMenu" src="../../images/icons.svg#icon-hamburger" alt="icon-hamburger" width="32" height="27">
              <img v-else src="../../images/icons.svg#icon-cross" alt="icon-cross" width="27" height="27"
                   class="--close">
            </a>
