</template>
<script>

export default {
  components: {},
  data() {
    return {
      headerScrollder: false,
      showMobileMenu: false,
      openMenus: [],
      telegram: this.$getContactData('telegram'),
      viberBotUrl: this.$getContactData('viberBotUrl'),
    }
  },
  methods: {
    // ...mapActions('modals', ['openBaseModal']),
    toggleMobileMenu() {
      this.showMobileMenu = !this.showMobileMenu;
      if (document.body.classList.contains('no-scroll')) {
        document.body.classList.remove('no-scroll')
      } else {
        document.body.classList.add('no-scroll')
      }
    },
    toggleMenu(menu) {
      if (this.openMenus.includes(menu)) {
        this.openMenus = this.openMenus.filter(elem => elem !== menu);
      } else {
        this.openMenus.push(menu)
      }
    },
    isOpen(menu) {
      return this.openMenus.includes(menu);
    },
    openTestPage() {
      window.open('https://test.winwin.education')
    }
  },
  mounted() {
    window.addEventListener('scroll', () => {
      let scrollPos = window.scrollY;
      let winHeight = window.innerHeight;
      let docHeight = document.documentElement.scrollHeight;
      let perc = 100 * scrollPos / (docHeight - winHeight);
      if (perc > 4) {
        this.headerScrollder = true;
      } else {
        this.headerScrollder = false;
      }
    });
  },
  watch: {
    $route() {
      this.showMobileMenu = false;
    }
  }
}
</script>
