<template>
    <footer id="footer" class="footer">
      <div class="footer_info">
        <div class="footer_contact-info">
          <div class="footer_contact-info_logo">
            <a href="/">
              <img src="../../images/logo.svg"
                   alt="WinWin Education | Твоя англійська FOREVER.">
            </a>
          </div>
          <p class="footer_contact-info_description">
            ФОП Герун Анна Анатоліївна <br> ЕДРПОУ: 3304116581. <br> WinWin Education - Твоя англійська <span class="footer_forever">FOREVER</span>.
          </p>
        </div>
        <div class="footer_links">
          <div class="footer_links_container">
            <div class="footer_links_item">
              <div><img src="../../images/icons.svg#icon-geo" alt="icon-geo"></div>
              <p>{{name}}, {{address}}</p>
            </div>
            <div class="footer_links_item">
              <div><img src="../../images/icons.svg#icon-mail" alt="icon-mail"></div>
              <p><a :href="`mailto:${email}`">{{email}}</a></p>
            </div>
          </div>
          <div class="footer_links_container">
            <div class="footer_links_item">
              <div><img src="../../images/icons.svg#icon-phone" alt="icon-phone"></div>
              <p><a :href="telephoneUrl">{{telephone}}</a></p>
            </div>
            <div class="footer_links_item">
              <div><img src="../../images/icons.svg#icon-site" alt="icon-site"></div>
              <p><a :href="websiteUrl">{{website}}</a></p>
            </div>
          </div>
        </div>
      </div>
      <div class="footer_bottom">
        <div class="footer_bottom_copyright">
          WinWin Education © 2020
        </div>
        <a href="#" class="footer_bottom_privacy">Згода на обробку персональних даних</a>
        <div class="footer_bottom_links">
          <a :href="instaUrl" target="_blank"><img src="../../images/icons.svg#icon-inst" alt="icon-inst"></a>
          <a :href="fbUrl" target="_blank"><img src="../../images/icons.svg#icon-facebook" alt="icon-facebook"></a>
          <a :href="telegram" target="_blank"><img src="../../images/icons.svg#icon-telegram" alt="icon-telegram"></a>
        </div>
      </div>
    </footer>
</template>

<script>
    export default {
        data() {
            return {
                instaUrl: this.$getContactData('insta'),
                fbUrl: this.$getContactData('fb'),
                telegramBotUrl: this.$getContactData('telegramBotUrl'),
                telegram: this.$getContactData('telegram'),
                messengerBotUrl: this.$getContactData('messengerBotUrl'),
                viberBotUrl: this.$getContactData('viberBotUrl'),
                email: this.$getContactData('email'),
                name: this.$getContactData('name'),
                address: this.$getContactData('address'),
                telephone: this.$getContactData('telephone'),
                telephoneUrl: this.$getContactData('telephoneUrl'),
                website: this.$getContactData('website'),
                websiteUrl: this.$getContactData('websiteUrl'),
            };
        }
    }
</script>

