const CHANGE_CONTACT = 'CHANGE_CONTACT';
const CHANGE_QUESTIONS = 'CHANGE_QUESTIONS';
const CHANGE_RESULT = 'CHANGE_RESULT';
const ADD_ANSWER = 'ADD_ANSWER';
const state = {
    contact: null,
    questions: [],
    answers: [],
    result: null
};

const mutations = {
    CHANGE_CONTACT(state, payload) {
        state.contact = payload;
    },
    CHANGE_QUESTIONS(state, payload) {
        state.questions = payload;
    },
    CHANGE_RESULT(state, payload) {
        state.result = payload;
    },
    ADD_ANSWER(state, payload) {
        state.answers.push(payload);
    }
};

const actions = {
    changeContact({commit}, payload) {
        if (payload) {
            commit(CHANGE_CONTACT, payload)
        }
    },
    changeQuestions({commit}, payload) {
        if (payload) {
            commit(CHANGE_QUESTIONS, payload)
        }
    },
    changeResult({commit}, payload) {
        if (payload) {
            commit(CHANGE_RESULT, payload)
        }
    },
    addAnswer({commit}, payload) {
        if (payload) {
            commit(ADD_ANSWER, payload)
        }
    }
};

const getters = {
    currentKey: (state) => {
        return state.answers.length;
    }
};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}
