import VueRouter from 'vue-router';
import store from './store/index';
let routes = [
	{ path: '/', name: 'start_page', component: require('./components/pages/start_page.vue').default },
	{ path: '/test', name: 'test_page', component: require('./components/pages/test_page.vue').default },
	{ path: '/result', name: 'result_page', component: require('./components/pages/result_page.vue').default },
];

let router = new VueRouter({
	mode: 'history',
    routes: routes,
	scrollBehavior () {
		return { x: 0, y: 0 }
	}
});
router.beforeResolve((to, from, next) => {
	// If this isn't an initial page load.
	if (to.name) {
		store.dispatch('changeLoadingStatus', {status: true});
	}
	next();
});

router.afterEach(() => {
	store.dispatch('changeLoadingStatus', {status: false});
});

export default router;
