<template>

    <div class="modal" v-if="isShowBaseModal">
        <transition name="modal">
            <div class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container">
                        <div class="modal-close" @click="closeBaseModal()">
                            <svg width="50" height="50" viewBox="0 0 50 50" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <rect width="50" height="50" rx="12" fill="white"/>
                                <path fill-rule="evenodd" clip-rule="evenodd" d="M25.5 26.8541L18.3541 34L16 31.6459L23.1459 24.5L16 17.3541L18.3541 15L25.5 22.1459L32.6459 15L35 17.3541L27.8541 24.5L35 31.6459L32.6459 34L25.5 26.8541Z" fill="#1A233F"/>
                            </svg>
                        </div>
                        <tmp-form :prop-data="baseModalEntity" :exam-data="(baseModalEntity && baseModalEntity['suggestedExam']) ? baseModalEntity['suggestedExam'] : null" :group-data="(baseModalEntity && baseModalEntity['suggestedGroup']) ? baseModalEntity['suggestedGroup'] : null" prop-title="Записатися на консультацію"></tmp-form>
<!--                        <img src="./../../images/images.svg#image-14" alt="#" class="modal-image-left">-->
<!--                        <img src="./../../images/images.svg#image-12" alt="#" class="modal-image-right">-->
<!--                        <img src="./../../images/images.svg#image-13" alt="#" class="modal-image-right-2">-->
                    </div>
                </div>
            </div>
        </transition>
    </div>

</template>

<script>
    import tmpForm from './form';
    import { mapState, mapActions } from 'vuex';
    export default {
        components: {tmpForm},
        data () {
            return {
                showModal: false,
            }
        },
        computed: {
            ...mapState('modals', ['isShowBaseModal', 'baseModalEntity'])
        },
        methods: {
            ...mapActions('modals', ['closeBaseModal'])
        },
    }
</script>
