<template>
  <tile class="loader" v-if="!showQuestion"></tile>
  <div class="container-fluid test_page" v-else>
    <div class="test_page_content">
      <div class="test_page_content_header">
        <h1>Дізнаємося свій рівень володіння <span>англійською мовою</span></h1>
        <div class="decorative-text">
          <span>Лишилось {{data.length - answers.length}} {{сountForm(data.length - answers.length, ['питання', 'питання', 'питань'])}}</span>
          <img src="images/homeBlock3text.png" alt="Block 3 icon">
        </div>
      </div>
      <div id="progress-bar-container">
        <div id="progress-bar" :style="{width: data.length * 29.75 + 'px'}">
          <span v-for="key in data.length" :class="{'active' : answers[key-1]}"></span>
        </div>
      </div>
      <div :key="currentKey" class="quick-test-content">
        <div class="quick-test-content-title">
          <template v-for="(text, key) in data[currentKey]['text']">
            <div style="display: inline-block;">
              <template v-if="text == '*'">
                <div class="answ" :class="{'active': (selectedItem && selectedItem['value'])}">
                  <span v-bind:key="text == '*' && selectedItem ? selectedItem['value'] : key">{{
                      text == '*' ? selectedItem ? selectedItem['value'] : '_ _ _' : text
                    }}</span>
                </div>
              </template>
              <template v-else>
                {{ text }}
              </template>
            </div>
          </template>
        </div>
        <div class="quick-test-question-box">
          <template v-for="value in data[currentKey]['answers']">
            <div class="quick-test-item"
                 :class="{'active' : selectedItem &&  value.text == selectedItem['value']}"
                 @click="selectAnswer(data[currentKey].id, data[currentKey].text, value.text)">
              <span>{{ value.text }}</span>
              <div class="checkbox"></div>
            </div>
          </template>
        </div>

        <div class="flex flex-v-h-center quick-test-buttons">
          <div class="btn-box">
            <div id="gtm-btn-quiz-next-question" class="btn btn-1" :disabled="!btnEnable"
                 :class="{'not-allowed' : !selectedItem}" @click="commitAnswer()">
              Наступне питання
            </div>
          </div>
          <div class="btn-box">
            <div id="gtm-btn-quiz-skip-question" class="btn btn-2"
                 @click="skipAnswer(data[currentKey].id, data[currentKey].text)">
              Пропустити
            </div>
          </div>
        </div>
        <p class="--subtitle">
          Уффф, вже тяжкувато.
          <a href="#" @click.prevent="submit">Закінчити тест та отримати результат</a>
        </p>
      </div>
    </div>
    <div class="contact-form-container">
      <div class="test_page_cat">
        <cat-gif v-if="gifs && gifs[currentKey]['url']" :gif="gifs[currentKey]['images']['original']['url']"></cat-gif>
      </div>
      <contact-us-form></contact-us-form>
    </div>
  </div>
</template>

<script>
import {mapActions, mapState, mapGetters} from "vuex";
import ContactUsForm from "../contactUsForm";
import CatGif from "../blocks/CatGif";

export default {
  components: {CatGif, ContactUsForm},
  props: {},
  data() {
    return {
      btnEnable: true,
      data: [],
      selectedItem: null,
      gifs: null,
      showQuestion: true
    }
  },
  computed: {
    ...mapState('test', ['contact', 'questions', 'answers']),
    ...mapGetters('test', ['currentKey'])
  },
  methods: {
    ...mapActions('test', ['changeContact', 'addAnswer', 'changeResult']),
    init() {
      this.data = this.questions;
      for (let i = 0; i < this.data.length; i++) {
        this.data[i]['text'] = this.data[i]['text'].replace(/_ {2,}/g, '_').replace(/_{2,}/g, '|*|');
        this.data[i]['text'] = this.data[i]['text'].split('|');
      }
    },

    /**
     *
     * @param id
     * @param question
     * @param value
     */
    selectAnswer(id, question, value) {
      this.selectedItem = {
        id: id,
        question: question.join(''),
        value: value
      };
    },

    commitAnswer() {
      if (!this.selectedItem) {
        return;
      }

      if (this.answers.some(e => e.id === this.data[this.currentKey].id)) {
        return;
      }
      this.addAnswer(this.selectedItem);
      this.selectedItem = null;
      if (this.answers.length === this.data.length) {
        this.btnEnable = false;
        this.submit();
        this.showQuestion = false
      }
    },

    /**
     *
     */
    skipAnswer(id, question) {
      if (this.answers.some(e => e.id === this.data[this.currentKey].id)) {
        // this.currentKey ++;
        return;
      }
      this.addAnswer({
        id: id,
        question: question,
        value: null
      });
      this.selectedItem = null;
      // this.currentKey ++;

      if (this.answers.length === this.data.length) {
        this.submit();
      } else {
        // this.currentKey ++;
      }
    },

    /**
     *
     * @param text
     * @param value
     * @returns {*}
     */
    replaceQuestionText(text, value) {
      return text.replace('<span>_ _ _</span>', '<span>' + value + '</span>');
    },

    /**
     *
     */
    submit() {
      if (this.answers.length > 0) {
        let _this = this;
        const payload = {
          quizAnswersLog: _this.answers,
          contactData: _this.contact,
          tracker: this.$wwSessionTracker.getTrackData()
        };
        this.axios.post(this.$getApiUrl('/api/quizv2/submit'), payload)
            .then(function (response) {
              _this.btnEnable = true;
              const responseData = response.data.data;
              _this.$gtm.trackEvent({
                event: 'gaEvent',
                eventCategory: 'English Test',
                eventAction: 'Finished Test',
                eventLabel: window.location.pathname + window.location.hash
              });
              // eslint-disable-next-line
              fbq('track', 'trackCustom', {
                content_name: 'Finished Test',
                content_category: 'English Test',
              });
              _this.changeResult({
                'levelTitle': responseData.level_title,
                'levelCode': responseData.level_code,
                'suggestedGroup': (typeof responseData.suggested_group !== 'undefined') ? responseData.suggested_group : null,
                'suggestedExam': (typeof responseData.suggested_exam !== 'undefined') ? responseData.suggested_exam : null,
                'quizRecordUuid': (typeof responseData.quiz_record_uuid !== 'undefined') ? responseData.quiz_record_uuid : {},
              });
              _this.$router.push({
                name: 'result_page'
              });
            })
            .catch(function (error) {
              _this.btnEnable = true;
              _this.changeResult({
                'levelTitle': 'Whoops',
                'levelCode': 'whoops',
                'suggestedGroup': null,
                'suggestedExam': null,
              });
              _this.$router.push({
                    name: 'result_page'
                  }
              );
            });
      } else {
        this.$router.push({name: 'start_page'});
      }
    },
    getJson(url, callback) {
      var xhr = new XMLHttpRequest();
      xhr.open('GET', url, true);
      xhr.responseType = 'json';
      xhr.onload = function () {
        var status = xhr.status;
        if (status === 200) {
          callback(null, xhr.response);
        } else {
          callback(status, xhr.response);
        }
      };
      xhr.send();
    },
    сountForm(number, titles) {
      number = Math.abs(number);
      if (Number.isInteger(number)) {
        let cases = [2, 0, 1, 1, 1, 2];
        return titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ];
      }
      return titles[1];
    },
  },
  created() {
    this.init();
    let _this = this;
    const url = 'https://api.giphy.com/v1/gifs/search?q=cute+cats&api_key=jtShcIy1ujEqOzHFFUil5VXb8Vhsj1JS'

    this.axios.all([
      this.axios.get(url + '&limit=15&offset=0'),
      this.axios.get(url + '&limit=15&offset=20')
    ]).then((responses) => {
      _this.gifs = [...responses[0].data.data, ...responses[1].data.data]
    }).catch(errors => {
    })

    if (this.answers.length === this.data.length) {
      this.submit(this.$event);
    }
  }
}
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .4s ease;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
