<template>
  <div class="form-base">
    <div class="form-base-header">
      {{ propTitle }}
    </div>
    <div class="form-base-body relative">
      <form action="">
        <div v-if="examData" class="form-block text-center form-text">
          <div>Ви обрали іспит:</div>
          <h2>{{ examData.full_title }} </h2>
          <div><span>{{ examData.exam_date }}</span></div>
        </div>
        <div v-if="groupData" class="form-block text-center form-text">
          <div>Ви обрали курс:</div>
          <h2 v-if="groupData.course">{{ groupData.course }} </h2>
          <h2 v-else>Underwater</h2>
          <div v-if="groupData.start_date">
            <span>{{ moment(groupData.start_date).format('DD-MM-YYYY hh:mm') }}</span>
          </div>
        </div>
        <div class="form-block text-center form-text">
          <div>A наші менеджери зв'яжуться з вами протягом декількох годин і розкажуть усі деталі.</div>
        </div>

        <div class="form-block">
          <i class="far fa-user-circle"></i>
          <input
              required
              v-model="data.name"
              type="text"
              name="name"
              id="name"
              class="input input-1"
              placeholder="Твоє ім'я?"
              :class="{'error' : validator.name === false}" @keypress="validate('name')">
        </div>

        <div class="form-block">
          <i class="fas fa-mobile-alt"></i>
          <input
              required
              v-model="data.phone"
              v-mask="'+38 (0##) ###-##-##'"
              type="tel"
              name="phone"
              id="phone"
              class="input input-1"
              placeholder="Твій номер телефону"
              :class="{'error' : validator.phone === false}" @keypress="validate('phone')">
        </div>

        <div class="form-block">
          <i class="fas fa-at"></i>
          <input v-model="data.email" type="email" name="email" id="email" class="input input-1"
                 placeholder="Твій Email">
        </div>

        <div class="text-center">
          <div class="btn-box inline-block">
            <button type="submit" class="button btn btn-1" :disabled="!btnEnable" @click="submit"
                    style="outline: none;">Надіслати
            </button>
          </div>
        </div>
      </form>
      <div class="form-success" v-if="response.success === true">
        <div class="form-success-title">Awesome!</div>
        <div class="form-success-descr paragraph">Отримали твій запит. Ми зв'яжемося якомога швидше.
        </div>
        <a href="#" class="btn btn-1" @click="close()">ОК!</a>
      </div>
    </div>
  </div>
</template>

<script>
import {mask} from 'vue-the-mask'
import {mapActions} from "vuex";

export default {
  directives: {mask},
  props: {
    propTitle: {
      type: String,
      required: true
    },
    propData: {
      required: false
    },
    groupData: {
      required: false
    },
    examData: {
      required: false
    },
  },
  data() {
    return {
      btnEnable: true,
      response: {error: null, success: null},
      data: {
        name: '',
        phone: '',
        email: '',
        source_id: this.$getSourceId(),
        city: this.$getContactData('cityId')
      },
      validator: {name: null, phone: null}
    }
  },
  methods: {
    ...mapActions('modals', ['closeBaseModal']),
    close() {
      this.response = {error: null, success: null};
      this.data = {
        name: '',
        phone: '',
        email: '',
        source_id: this.$getSourceId(),
        city: this.$getContactData('cityId')
      };
      this.closeBaseModal();
    },
    submit(e) {
      const examData = this.examData ? this.examData : false;
      const groupData = this.groupData ? this.groupData : false;

      this.validator = {name: null, phone: null};
      if (this.data.name && this.data.phone && this.data.email && this.validator.name !== false && this.validator.phone !== false) {
        this.btnEnable = false;
        let _this = this;
        const payload = {
          lead: _this.data,
          tracker: this.$wwSessionTracker.getTrackData()
        };
        if (examData) {
          payload.lead['exam_data'] = examData;
        }
        if (groupData) {
          payload.lead['group_data'] = groupData;
        }
        this.axios.post(this.$getApiUrl('/api/lead/submit'), payload)
            .then(function (response) {
              _this.response.success = true;
              _this.btnEnable = true;
              _this.$gtm.trackEvent({
                event: 'gaEvent',
                eventCategory: 'English Test',
                eventAction: 'Submit Lead via PopForm',
                eventLabel: window.location.pathname + window.location.hash
              });
            })
            .catch(function (error) {
              _this.btnEnable = true;
            });
        e.preventDefault();
      } else {
        if (!this.data.phone || this.data.phone.length < 19) {
          this.validator.phone = false;
        }
        if (!this.data.name || this.data.name.length < 2) {
          this.validator.name = false;
        }
      }
    },
    validate(type) {
      switch (type) {
        case 'name':
          if (this.data.name && this.data.name.length >= 2) {
            this.validator.name = true;
          }
          break;
        case 'phone':
          if (this.data.phone && this.data.phone.length >= 19) {
            this.validator.phone = true;
          }
          break;
      }
    }
  }
}
</script>
