<template>
  <div class="contactUs-container">
    <div class="contactUs">
      <img class="contactUs_question" src="../../images/formQuestion.svg" alt="question">
      <form class="contactUs_form">
        <div class="contactUs_form_inputs">
          <div class="input-group">
            <label for="name">Ім'я <span class="required">*</span></label>
            <input type="text" name="name" id="name" placeholder="Як до Вас звертатись?" required
                   :class="{ 'form-group--error': $v.data.name.$error }"
                   v-model="$v.data.name.$model">
          </div>
          <div class="input-group">
            <label for="lastName">Прізвище <span class="required">*</span></label>
            <input type="text" name="lastName" id="lastName" placeholder="Ваше прізвище" required
                   :class="{ 'form-group--error': $v.data.lastName.$error }"
                   v-model="$v.data.lastName.$model">
          </div>
          <div class="input-group">
            <label for="phone">Номер телефону <span class="required">*</span></label>
            <input type="text" name="phone" id="phone" v-mask="'+38 (0##) ###-##-##'"
                   placeholder="+380 - - -    - -    - -" required
                   :class="{ 'form-group--error': $v.data.phone.$error }"
                   v-model.trim="$v.data.phone.$model">
          </div>
          <button class="btn btn-1" :disabled="disableBtn" @click="submit">
            Записатися
          </button>
          <div class="input-group_checkbox">
            <input type="checkbox" name="agreed" id="agreed" required
                   :class="{ 'form-group--error': $v.data.agreed.$error }"
                   v-model="$v.data.agreed.$model">
            <label for="agreed"><span>Я погоджуюся з умовами та положеннями</span></label>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import {mask} from "vue-the-mask";
import {validationMixin} from "vuelidate";
import {email, minLength, required} from "vuelidate/lib/validators";

export default {
  directives: {mask},
  mixins: [validationMixin],
  validations: {
    data: {
      name: {
        required
      },
      lastName: {
        required
      },
      agreed: {
        required,
      },
      phone: {
        required,
        minLength: minLength(19)
      },
    }
  },
  validationGroup: ['data.name', 'data.lastName', 'data.phone'],
  data() {
    return {
      response: {error: null, success: null},
      data: {
        name: '',
        lastName: '',
        phone: '',
        agreed: true,
        city: this.$getContactData('cityId')
      },
      validator: {name: null, lastName: null, phone: null, agreed: false},
      disableBtn: false
    };
  },
  methods: {
    submit(e) {
      e.preventDefault();
      this.$v.$touch();
      if (this.data.name && this.data.lastName && this.data.phone && this.data.agreed && !this.$v.data.agreed.$error && !this.$v.data.name.$error && !this.$v.data.lastName.$error && !this.$v.data.phone.$error) {
        this.disableBtn = true;
        let _this = this;
        const payload = {
          lead: {
            name: _this.data.name + ' ' + _this.data.lastName,
            email: 'test@test.com',
            phone: _this.data.phone,
            additional_data: null,
            service_type: 'ENGLISH_TEST',
            source_id: 10,
            city_id: _this.data.city
          },
        };
        this.axios.post(this.$getApiUrl('/api/lead/submit'), payload)
            .then(function () {
              _this.disableBtn = false;
              _this.clearForm();
            })
            .catch(function () {
              _this.disableBtn = false;
              alert('Ой, у нас баги :( Щось пішло зовсім не так. Спробуйте перезавантажити сторінку.')
            });
      }
    },
    clearForm() {
      setTimeout(() => {
        this.data.lastName = '';
        this.data.agreed = false;
        this.data.name = '';
        this.data.phone = '';
        this.$v.$reset();
      }, 1000)
    }
  },
}
</script>

