import moment from 'moment'

Vue.prototype.moment = moment;
const isProd = process.env.NODE_ENV === 'production';

//import html pages into bundle
import '../index.html';

import axios from 'axios'
import VueAxios from 'vue-axios'
import VueGtm from 'vue-gtm';
import VueRouter from 'vue-router'


import Vue from 'vue';
import modal from './components/modal.vue';
import tmpForm from './components/form.vue';

import footerLinks from './components/footerLinks.vue';
import headerLinks from './components/headerLinks.vue';
import headerNav from './components/headerNav.vue';
import wwSessionTracker from './ww-tracker';

import router from './route.js';
import store from './store';
import * as contactData from './jsons/contacts.json';

import VueSpinners from 'vue-spinners';

Vue.use(VueRouter)
Vue.use(VueSpinners)
Vue.prototype.$wwSessionTracker = wwSessionTracker;

Vue.prototype.$getApiUrl = (path) => {
    //return 'http://localhost:10100' + path;
    return 'https://management.winwin.education' + path;
};

Vue.prototype.$getContactData = (key) => {
    switch (localStorage.getItem('ww_city')) {
        case 'online':
            return contactData.online[key];
        case 'zhytomyr':
            return contactData.zhytomyr[key];
        default:
            return contactData.zhytomyr[key];
    }
};

Vue.prototype.$getSourceId = () => {
    return 60; //constant Website [NEW TEST]
};

Vue.config.productionTip = !isProd;
Vue.use(VueAxios, axios);

Vue.use(VueGtm, {
    id: 'GTM-5JQFTGV', enabled: isProd, debug: !isProd, loadScript: true, vueRouter: router
});

const app = new Vue({
    components: {modal, tmpForm, footerLinks, headerLinks, headerNav},
    el: '#app',
    mode: 'history',
    router,
    store,
    data() {
        return {
            headerScrollder: false, showMobileMenu: false
        }
    },
    methods: {
        toggleMobileMenu() {
            this.showMobileMenu = !this.showMobileMenu;
            if (document.body.classList.contains('no-scroll')) {
                document.body.classList.remove('no-scroll')
            } else {
                document.body.classList.add('no-scroll')
            }
        }, location(href, newWindow = false) {
            if (newWindow) {
                window.open(href);
            } else {
                document.location = href;
            }
        }
    },
    beforeCreate: function () {
        let wwCity = localStorage.getItem('ww_city');
        if (wwCity === null) {
            const search = (window.location.search === undefined) ? '' : window.location.search;

            if (search.indexOf('zt') !== -1) {
                wwCity = 'zhytomyr';
            } else if (search.indexOf('online') !== -1) {
                wwCity = 'online';
            } else {
                wwCity = 'zhytomyr';
            }
            localStorage.setItem('ww_city', wwCity);
        }
        this.$wwSessionTracker.track();
    },
    mounted() {
        let vm = this;
        localStorage.setItem('utm_source', vm.$route.query?.utm_source);
        localStorage.setItem('utm_medium', vm.$route.query?.utm_medium);
        localStorage.setItem('utm_campaign', vm.$route.query?.utm_campaign);

        // Set style to header when scrolled
        window.addEventListener('scroll', function (e) {
            let scrollPos = window.scrollY;
            let winHeight = window.innerHeight;
            let docHeight = document.documentElement.scrollHeight;
            let perc = 100 * scrollPos / (docHeight - winHeight);
            if (perc > 4) {
                vm.headerScrollder = true;
            } else {
                vm.headerScrollder = false;
            }
        });
        // End block

        Array.prototype.max = function () {
            return Math.max.apply(null, this);
        };

    }
});
